:root {
  --bg: #fefefb;
  --text: #3d3d3d;
  --text-light: #fff;
  --color-primary: #db3333;
  --color-secondary: pink;
  --color-tertiary: green;
  --color-light: #fff;
  --color-dark: #111;
  --radius: 10px;
  --maxw: 72rem;
  --content-gap: clamp(.75rem, 1.5vw, 1.5rem);
  --content-gap-cards: clamp(.5rem, 1.3vw, 1.25rem);
  --gutter: clamp(1.5rem, 4vw, 3rem);
  --section-space: clamp(1.5rem, 6vw, 5rem);
  --flow: clamp(3rem, 6vw, 7.5rem);
  --pad-header-y: clamp(.75rem, 2vw, 1.5rem);
  --pad-header-x: clamp(1rem, 3vw, 2rem);
  --pad-main-bottom: clamp(2.5rem, 6vw, 4rem);
  --pad-card: clamp(.85rem, 1.5vw, 1.25rem);
  --pad-btn-y: clamp(.6rem, 1vw, .9rem);
  --pad-btn-x: clamp(1rem, 1.8vw, 1.5rem);
  --pad-footer-top: clamp(1.5rem, 3vw, 2rem);
  --pad-footer-bottom: clamp(.75rem, 2vw, 1.25rem);
}

html {
  box-sizing: border-box;
  background-color: var(--bg);
  scroll-behavior: smooth;
  height: -webkit-fill-available;
  font-size: 16px;
  line-height: 1.5;
  overflow: hidden auto;
}

*, :before, :after {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
}

body {
  height: 100%;
  color: var(--text);
  background: var(--bg);
  font-family: Inter, system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
}

main {
  padding-bottom: var(--pad-main-bottom);
  flex-direction: column;
  display: flex;
}

img, svg, picture {
  vertical-align: middle;
  max-width: 100%;
  height: auto;
}

h1, h2, h3, h4, h5 {
  text-wrap: balance;
  word-wrap: break-word;
  font-family: DM Serif Display, serif;
  font-weight: 700;
  line-height: 1.25;
}

p, li {
  letter-spacing: .02em;
  text-wrap: balance;
  word-wrap: break-word;
  max-width: 65ch;
  font-size: max(.95rem, min(1vw + .3rem, 1.1rem));
}

h1 {
  font-size: max(2rem, min(5vw + .5rem, 4rem));
}

h2 {
  font-size: max(1.8rem, min(4vw + .5rem, 3.2rem));
}

h3 {
  font-size: max(1.6rem, min(3vw + .4rem, 2.6rem));
}

h4 {
  font-size: max(1.4rem, min(2.5vw + .3rem, 2rem));
}

h5 {
  font-size: max(1.3rem, min(2vw + .3rem, 1.6rem));
}

#title-header {
  color: #fff;
  font-family: Inter, system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  font-size: max(1.3rem, min(2vw + .3rem, 1.6rem));
}

.arrows {
  width: 80px;
  height: 80px;
  margin-left: -20px;
  position: absolute;
  bottom: 20px;
  left: 50%;
}

.arrows path {
  stroke: #fff;
  fill: #0000;
  stroke-width: 3px;
  -webkit-animation: 2s infinite arrow;
  animation: 2s infinite arrow;
}

@-webkit-keyframes arrow {
  0% {
    opacity: 0;
    -webkit-transform: translateY(10px);
    transform: translateY(10px);
  }

  80% {
    opacity: 1;
    -webkit-transform: translateY(50px);
    transform: translateY(50px);
  }

  100% {
    opacity: 0;
    -webkit-transform: translateY(50px);
    transform: translateY(50px);
  }
}

@keyframes arrow {
  0% {
    opacity: 0;
    -webkit-transform: translateY(10px);
    transform: translateY(10px);
  }

  80% {
    opacity: 1;
    -webkit-transform: translateY(50px);
    transform: translateY(50px);
  }

  100% {
    opacity: 0;
    -webkit-transform: translateY(50px);
    transform: translateY(50px);
  }
}

.arrows path.a1 {
  -webkit-animation-delay: -.5s;
  animation-delay: -.5s;
}

header {
  z-index: 10;
  width: 100%;
  padding-block: var(--pad-header-y);
  padding-inline: var(--pad-header-x);
  color: #fff;
  background-color: #0000004d;
  justify-content: space-between;
  align-items: center;
  transition: background-color .3s, -webkit-backdrop-filter .3s, backdrop-filter .3s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

header.scrolled {
  backdrop-filter: blur(12px);
  background-color: #000000b3;
}

nav a {
  color: inherit;
  margin-left: max(.75rem, min(1.5vw, 1.25rem));
}

nav a:hover {
  text-decoration: underline;
}

@media (width <= 540px) {
  header {
    gap: var(--content-gap);
    flex-direction: column;
  }

  nav a {
    margin-left: 0;
  }
}

.hero {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
  overflow: hidden;
}

.home {
  background-image: -webkit-image-set(url("Background_plants-large-800.03557c97.webp") 1x, url("Background_plants-large-1200.30a9b4ec.webp") 2x);
  background-image: image-set("Background_plants-large-800.03557c97.webp" 1x, "Background_plants-large-1200.30a9b4ec.webp" 2x);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
}

.secondary {
  height: max(12rem, min(40vh, 25rem));
}

@-webkit-keyframes appearUp {
  from {
    opacity: 0;
    -webkit-transform: translateY(1rem);
    transform: translateY(1rem);
  }

  to {
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}

@keyframes appearUp {
  from {
    opacity: 0;
    -webkit-transform: translateY(1rem);
    transform: translateY(1rem);
  }

  to {
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}

.hero__content {
  z-index: 2;
  text-align: center;
  align-items: center;
  gap: var(--content-gap);
  max-width: 85ch;
  color: var(--text-light);
  flex-direction: column;
  padding-left: max(1rem, min(4vw, 2rem));
  padding-right: max(1rem, min(4vw, 2rem));
  display: flex;
  position: relative;
}

.hero__content > * {
  opacity: 0;
  -webkit-animation: 1s ease-out forwards appearUp;
  animation: 1s ease-out forwards appearUp;
}

.hero__content > :first-child {
  -webkit-animation-delay: .1s;
  animation-delay: .1s;
}

.hero__content > :nth-child(2) {
  -webkit-animation-delay: .4s;
  animation-delay: .4s;
}

.hero__content > :nth-child(3) {
  -webkit-animation-delay: .8s;
  animation-delay: .8s;
}

.hero__img {
  z-index: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  inset: 0;
}

.hero__img img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  -webkit-animation: 5s ease-out forwards slowZoom;
  animation: 5s ease-out forwards slowZoom;
  display: block;
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}

@-webkit-keyframes slowZoom {
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

@keyframes slowZoom {
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

.categoria {
  background-image: -webkit-image-set(url("Background_plants-large-800.03557c97.webp") 1x, url("Background_plants-large-1200.30a9b4ec.webp") 2x);
  background-image: image-set("Background_plants-large-800.03557c97.webp" 1x, "Background_plants-large-1200.30a9b4ec.webp" 2x);
  background-position: center;
  background-size: cover;
}

.container {
  max-width: var(--maxw);
  padding-inline: var(--gutter);
  margin-left: auto;
  margin-right: auto;
}

.container_content {
  align-items: center;
  gap: var(--content-gap);
  display: flex;
}

.container_content.right {
  flex-direction: row;
}

.container_content.left {
  flex-direction: row-reverse;
}

@media (width <= 768px) {
  .container_content.right, .container_content.left {
    flex-direction: column;
  }
}

.content__media {
  background: var(--color-secondary);
  color: var(--text);
  grid-template-columns: 1fr;
  align-items: center;
  display: grid;
}

.content__figure {
  z-index: 1;
  position: relative;
}

.figure {
  aspect-ratio: 4 / 5;
  margin: 0;
  position: relative;
  overflow: hidden;
  box-shadow: 0 8px 24px #00000014;
}

.content__content {
  text-align: left;
  align-items: flex-start;
  gap: var(--content-gap);
  flex-direction: column;
  max-width: 85ch;
  display: flex;
}

.video {
  text-align: center;
  align-items: center;
  gap: var(--content-gap);
  flex-direction: column;
  display: flex;
}

.video lite-youtube {
  width: 100%;
  max-width: 720px;
}

.btn {
  letter-spacing: .02rem;
  padding-block: var(--pad-btn-y);
  padding-inline: var(--pad-btn-x);
  border-radius: var(--radius);
  color: var(--color-primary);
  cursor: pointer;
  background: none;
  border: 1px solid;
  margin-top: max(.4rem, min(.8vw, .75rem));
  font-size: max(1rem, min(1vw + .3rem, 1.25rem));
  font-weight: 500;
  transition: -webkit-transform .25s, transform .25s, box-shadow .25s, background-color .25s, color .25s;
  display: inline-block;
}

.btn--light {
  color: var(--color-light);
  border-color: var(--color-light);
}

.btn--light:hover {
  background: var(--color-light);
  color: var(--color-dark);
}

.btn--dark {
  color: var(--color-primary);
  border-color: var(--color-primary);
}

.btn--dark:hover {
  background: var(--color-primary);
  color: var(--color-light);
}

.btn:hover {
  -webkit-transform: translateY(-3px);
  transform: translateY(-3px);
  box-shadow: 0 2px 6px #00000026;
}

.rincones {
  gap: var(--content-gap);
  flex-direction: column;
  display: flex;
}

.grid {
  gap: var(--content-gap);
  grid-template-columns: 1fr;
  display: grid;
}

@media (width >= 640px) {
  .grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (width >= 1024px) {
  .grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

.card__link {
  color: var(--color-primary);
  transition: -webkit-transform .4s, transform .4s, box-shadow .4s;
  display: block;
}

.card {
  justify-content: space-between;
  gap: var(--content-gap-cards);
  padding: var(--pad-card);
  border-radius: var(--radius);
  background: var(--color-light);
  flex-direction: column;
  height: 100%;
  text-decoration: none;
  transition: -webkit-transform .4s, transform .4s, box-shadow .4s;
  display: flex;
}

.card:hover, .card:focus-within {
  -webkit-transform: translateY(-6px)scale(1.01);
  transform: translateY(-6px)scale(1.01);
  box-shadow: 0 1rem 2rem #00000026;
}

.card__media {
  border-radius: var(--radius);
  overflow: hidden;
}

.card__media img {
  aspect-ratio: 1;
  object-fit: cover;
  width: 100%;
  height: auto;
  display: block;
}

.card h3, .card p {
  color: var(--text);
  margin: 0;
}

.card__link:hover {
  color: var(--color-primary);
  -webkit-transform: translateY(-5px);
  transform: translateY(-5px);
  box-shadow: 0 12px 28px #00000026;
}

.card__link:hover .card__cta {
  text-decoration: underline;
}

.site-footer {
  background: var(--color-dark);
  color: var(--color-light);
  padding-block: var(--pad-footer-top) var(--pad-footer-bottom);
  -webkit-clip-path: polygon(0% 0%, 100% 50%, 100% 100%, 0 100%);
  clip-path: polygon(0% 0%, 100% 50%, 100% 100%, 0 100%);
}

@media (width <= 768px) {
  .site-footer {
    -webkit-clip-path: polygon(0% 0%, 500% 50%, 100% 100%, 0 100%);
    clip-path: polygon(0% 0%, 500% 50%, 100% 100%, 0 100%);
  }
}

.footer__content {
  gap: var(--content-gap);
  justify-content: space-between;
  align-items: flex-end;
  display: flex;
}

.footer__title {
  font-size: max(1.1rem, min(1.5vw, 1.3rem));
  font-weight: 700;
}

.footer__tagline {
  color: #ffffffb3;
}

.footer__list {
  flex-wrap: wrap;
  gap: max(.5rem, min(1vw, .75rem));
  list-style: none;
  display: flex;
}

.footer__list a {
  color: var(--color-light);
}

.footer__list a:hover, .footer__list a:focus-visible {
  text-decoration: underline;
}

@media (width <= 768px) {
  .footer__content {
    text-align: center;
    flex-direction: column;
    align-items: center;
  }
}

.skip-link {
  color: #fff;
  z-index: 1000;
  background: #000;
  border-radius: 8px;
  outline: none;
  padding: .6rem 1rem;
  text-decoration: none;
  transition: -webkit-transform .2s, transform .2s;
  position: absolute;
  top: .5rem;
  left: .5rem;
  -webkit-transform: translateY(-150%);
  transform: translateY(-150%);
}

.skip-link:focus, .skip-link:focus-visible {
  -webkit-transform: translateY(0);
  transform: translateY(0);
}

.skip-link:hover {
  text-decoration: underline;
}

@media (prefers-reduced-motion: reduce) {
  .skip-link {
    transition: none;
  }
}

.visually-hidden {
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  position: absolute !important;
}

:focus-visible {
  outline: 3px solid var(--color-primary);
  outline-offset: 4px;
  border-radius: var(--radius);
  scroll-margin-top: max(4rem, min(12vh, 6rem));
  transition: outline .2s;
}

button:focus-visible, a:focus-visible, input:focus-visible, textarea:focus-visible {
  outline: 3px solid var(--color-primary);
  outline-offset: 3px;
  border-radius: var(--radius);
}

section {
  margin: 0;
}

main > section + section {
  margin-top: var(--section-space);
}
